body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contenedor {
  max-width: 80%;
  height: 100vh;
  margin: 0 auto;
  padding: 2rem;
}


.button {
  background-color: black;
  padding: 6px;
  border-radius: 5px;
  text-decoration: none;
  width: 150px;
}

.button:hover {
  background-color: rgb(89, 77, 77);
}

img {
  width: 145px;
  height: 165px;
}

nav {
  display: flex;
  background-color: rgb(0, 0, 0);
  color: white;
}

nav ul {
  display: flex;
  margin-top: 30px;
  text-decoration: none;
  list-style: none;
}

nav li {
  margin-left: 80px;
}

.btn-buscar {
  margin-left: 2px;
  height: 30px;
}

nav img {
  height: 100px;
}

.carousel img {
  height: 350px;
}

body {
  background-color: rgb(218, 217, 217);
}

.body {
  background-color: rgb(247, 240, 240);
  padding: 30px;
  border-radius: 10px;
  height: 80%;
}

.img-product {
  width: 95%;
  height: 305px;
  border-radius: 5px;
  margin-top: 10px;
  margin-left: 10px;
}

.articlesect h4 {
  display: flex;
}

.articlesect {
  display: flex;
}


.desc-product {
  font-size: small;
  margin-top: 32px;
  margin-left: 20px;
  width: 100%;
  font-family: Arial, sans-serif;
}

.title-product {
  font-size: 30px;
  text-align: center;
}

.brand-product {
  margin-left: 40px;
  font-size: 25px;
}

.date-product {
  margin-left: 350px;
}

hr {
  color: rgb(152, 151, 151);
  margin-left: 20px;
  width: 690px;
}

.btn-car {
  margin-left: 550px;
}

.sect-datos {
  margin-top: 25px;
}

.star {
  color: rgb(241, 178, 20);
}

.star-section {
  margin-left: 20px;
}

.img-oneproduct {
  width: 233px;
}

.product-things {
  margin-bottom: 25px;
  border-radius: 8px;
}

.product-things img {
  border-radius: 5px;
  height: 230px;
}

.product-things h3 {
  font-size: 18px;
  text-align: center;
  margin-top: 9px;
}

.price-sect {
  text-align: end;
  margin-right: 15px;
  margin-bottom: 12px;
}